export const isArray = Array.isArray;

export const extend = Object.assign;

// Âà§Êñ≠‰∏Ä‰∏™Â≠óÁ¨¶‰∏≤ÊòØÂê¶ÊòØÂêàÊ≥ïÁöÑÂèòÈáèÊ†áËØÜÁ¨¶Ôºà‰∏çËÉΩÂåÖÂê´Êìç‰ΩúÁ¨¶„ÄÅÁÇπ„ÄÅÊã¨Âè∑Á≠âÔºâ
function isSimpleIdentifier(exp: string): boolean {
  return /^[A-Za-z_$][\w$]*$/.test(exp);
}

// Âà§Êñ≠Ë°®ËææÂºèÊòØÂê¶ÊòØ‚ÄúÈùôÊÄÅÁöÑ‚ÄùÔºàÂç≥ÂèØ‰ª•Âú®ÁºñËØëÊúüÁ°ÆÂÆöÁöÑÔºâ
export function isStaticExpression(exp: string): boolean {
  // 1. Â¶ÇÊûúÊòØÂêàÊ≥ïÁöÑÂèòÈáèÂêçÔºöÈùôÊÄÅÔºàÂ¶Ç @click="handleClick"Ôºâ
  if (isSimpleIdentifier(exp)) {
    return true;
  }

  // 2. Â¶ÇÊûúÂåÖÂê´ÂáΩÊï∞Ë∞ÉÁî®„ÄÅÊìç‰ΩúÁ¨¶„ÄÅÁÆ≠Â§¥ÂáΩÊï∞Á≠âÔºåËÆ§‰∏∫ÊòØÂä®ÊÄÅ
  // ËøôÂè™ÊòØ‰∏Ä‰∏™ÁÆÄÂçïÁöÑÂêØÂèëÂºèÂà§Êñ≠
  const dynamicKeywords = ['(', ')', '=>', '+', '-', '*', '/', '.', '[', ']'];

  for (const keyword of dynamicKeywords) {
    if (exp.includes(keyword)) {
      return false;
    }
  }

  // ÈªòËÆ§ËÆ§‰∏∫ÊòØÈùôÊÄÅ
  return true;
}
