const is = {
  Array: Array.isArray,
  Date: (val) => val instanceof Date,
  Set: (val) => Object.prototype.toString.call(val) === '[object Set]',
  Map: (val) => Object.prototype.toString.call(val) === '[object Map]',
  Object: (val) => Object.prototype.toString.call(val) === '[object Object]',
  Symbol: (val) => Object.prototype.toString.call(val) === '[object Symbol]',
  Function: (val) => Object.prototype.toString.call(val) === '[object Function]',
};

export function deepClone(value, weakMap = new WeakMap(), strict = false) {
  // 2.1 ÂáΩÊï∞ÊµÖÊã∑Ë¥ù
  /* if (is.Function(value)) return value */

  // 2.2 ÂáΩÊï∞Ê∑±Êã∑Ë¥ù
  if (is.Function(value) && strict) {
    if (/^function/.test(value.toString()) || /^\(\)/.test(value.toString())) {
      return new Function('return ' + value.toString())();
    }

    return new Function('return function ' + value.toString())();
  } else if (is.Function(value)) {
    return value;
  }

  // 3.Date Ê∑±Êã∑Ë¥ù
  if (is.Date(value)) return new Date(value.valueOf());

  // 4.Âà§Êñ≠Â¶ÇÊûúÊòØSymbolÁöÑvalue, ÈÇ£‰πàÂàõÂª∫‰∏Ä‰∏™Êñ∞ÁöÑSymbol
  if (is.Symbol(value)) return Symbol(value.description);

  // 5.Âà§Êñ≠ÊòØÂê¶ÊòØSetÁ±ªÂûã ËøõË°åÊ∑±Êã∑Ë¥ù
  if (is.Set(value)) {
    // 5.1 ÊµÖÊã∑Ë¥ù Áõ¥Êé•ËøõË°åËß£ÊûÑÂç≥ÂèØ
    // return new Set([...value])

    // 5.2 Ê∑±Êã∑Ë¥ù
    const newSet = new Set();
    for (const item of value) {
      // @ts-expect-error
      newSet.add(deepClone(item), weakMap);
    }
    return newSet;
  }

  // 6.Âà§Êñ≠ÊòØÂê¶ÊòØMapÁ±ªÂûã
  if (is.Map(value)) {
    // 6.1 ÊµÖÊã∑Ë¥ù Áõ¥Êé•ËøõË°åËß£ÊûÑÂç≥ÂèØ
    // return new Map([...value])

    // 6.2 Ê∑±Êã∑Ë¥ù
    const newMap = new Map();
    for (const item of value) newMap.set(deepClone(item[0], weakMap), deepClone(item[1], weakMap));
    return newMap;
  }

  // 9.Âà§Êñ≠weakMapÊòØÂê¶ÊúâÂÄº ÊúâÂÄºÁöÑÊÉÖÂÜµ‰∏ãÂ∞±Áõ¥Êé•Â∞ÜÂÄºËøîÂõûÂ∞±ÂèØ‰ª•
  if (weakMap.has(value)) return weakMap.get(value);

  // 11.2 Âà§Êñ≠Êï∞ÁªÑ
  if (is.Array(value)) {
    const newArr = [];
    // eslint-disable-next-line @typescript-eslint/no-for-in-array
    for (const item in value) newArr[item] = deepClone(value[item], weakMap);
    return newArr;
  }

  // 1.Â¶ÇÊûú‰∏çÊòØÂØπË±°Á±ªÂûãÂàôÁõ¥Êé•Â∞ÜÂΩìÂâçÂÄºËøîÂõû
  if (!is.Object(value)) return value;

  // 7.Âà§Êñ≠‰º†ÂÖ•ÁöÑÂØπË±°ÊòØÊï∞ÁªÑ, ËøòÊòØÂØπË±°
  const newObj = is.Array(value) ? [] : {};

  // 10.ÂΩìweakMapÊ≤°ÊúâÂÄºÊó∂ÔºåÂ∞ÜoriginValue‰Ωú‰∏∫key, newObj‰Ωú‰∏∫value
  weakMap.set(value, newObj);

  for (const key in value) {
    // 11.1 Âà§Êñ≠Êï∞ÁªÑ
    if (is.Array(value[key])) deepClone(value[key], weakMap);

    weakMap.set(value, newObj);
    // 8 ËøõË°åÈÄíÂΩíË∞ÉÁî®
    newObj[key] = deepClone(value[key], weakMap);
  }

  // 4.1 ÂØπSymbol‰Ωú‰∏∫keyËøõË°åÁâπÊÆäÁöÑÂ§ÑÁêÜ ÊãøÂà∞ÂØπË±°‰∏äÈù¢ÁöÑÊâÄÊúâSymbol keyÔºå‰ª•Êï∞ÁªÑÂΩ¢ÂºèËøîÂõû
  const symbolKeys = Object.getOwnPropertySymbols(value);
  for (const sKey of symbolKeys) {
    // 4.2 ËøôÈáåÊ≤°ÊúâÂøÖË¶ÅÂàõÂª∫‰∏Ä‰∏™Êñ∞ÁöÑSymbol
    // const newSKey = Symbol(sKey.description)

    // 4.3 Áõ¥Êé•Â∞ÜÂéüÊù•ÁöÑSymbol key Êã∑Ë¥ùÂà∞Êñ∞ÂØπË±°‰∏äÂ∞±ÂèØ‰ª•‰∫Ü
    newObj[sKey] = deepClone(value[sKey], weakMap);
  }

  return newObj;
}

export function guid(): string {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    const r = (Math.random() * 16) | 0;
    const v = c === 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

export function addCSS(cssText) {
  const style = document.createElement('style'); // ÂàõÂª∫‰∏Ä‰∏™styleÂÖÉÁ¥†
  const head = document.head || document.getElementsByTagName('head')[0]; // Ëé∑ÂèñheadÂÖÉÁ¥†
  style.type = 'text/css'; // ËøôÈáåÂøÖÈ°ªÊòæÁ§∫ËÆæÁΩÆstyleÂÖÉÁ¥†ÁöÑtypeÂ±ûÊÄß‰∏∫text/cssÔºåÂê¶ÂàôÂú®ie‰∏≠‰∏çËµ∑‰ΩúÁî®
  // @ts-expect-error
  if (style.styleSheet) {
    // IE
    const func = function () {
      try {
        // @ts-expect-error
        style.styleSheet.cssText = cssText;
      } catch (e) {}
    };
    // @ts-expect-error
    if (style.styleSheet.disabled) {
      setTimeout(func, 10);
    } else {
      func();
    }
  } else {
    // w3c
    // w3cÊµèËßàÂô®‰∏≠Âè™Ë¶ÅÂàõÂª∫ÊñáÊú¨ËäÇÁÇπÊèíÂÖ•Âà∞styleÂÖÉÁ¥†‰∏≠Â∞±Ë°å‰∫Ü
    const textNode = document.createTextNode(cssText);
    style.appendChild(textNode);
  }
  head.appendChild(style); // ÊääÂàõÂª∫ÁöÑstyleÂÖÉÁ¥†ÊèíÂÖ•Âà∞head‰∏≠
}

