import { queueJob } from './scheduler';

type EffectFn = () => void;

const targetMap = new WeakMap<object, Map<string | symbol, Set<EffectFn>>>();
let activeEffect: EffectFn | null = null;

export function effect(fn: EffectFn) {
  const wrapper = () => {
    activeEffect = wrapper;
    fn();
    activeEffect = null;
  };
  wrapper();
  return wrapper;
}

export function track(target: object, key: string | symbol) {
  if (!activeEffect) return;
  let depsMap = targetMap.get(target);
  if (!depsMap) {
    depsMap = new Map();
    targetMap.set(target, depsMap);
  }

  let deps = depsMap.get(key);
  if (!deps) {
    deps = new Set();
    depsMap.set(key, deps);
  }

  deps.add(activeEffect);
}

export function trigger(target: object, key: string | symbol) {
  const depsMap = targetMap.get(target);
  if (!depsMap) return;

  const effects = depsMap.get(key);
  if (!effects) return;

  for (const effect of effects) {
    queueJob(effect); // ÂÖ®Â±ÄË∞ÉÂ∫¶
  }
}

// watch ÂÆûÁé∞
function watch(source: any, cb: Function) {
  // ÂÆö‰πâ‰∏Ä‰∏™ÂÜÖÈÉ®ÂâØ‰ΩúÁî®ÂáΩÊï∞ÔºåËØªÂèñsourceËß¶Âèë‰æùËµñÊî∂ÈõÜ
  let getter: any;

  if (typeof source === 'function') {
    getter = source;
  } else if (isRef(source)) {
    getter = () => source.value;
  } else {
    // ÊîØÊåÅ reactive ÂØπË±°
    getter = () => traverse(source);
  }

  let oldValue: any;

  // ÂÆö‰πâÂâØ‰ΩúÁî®ÂáΩÊï∞ÔºåËß¶ÂèëgetterËé∑ÂèñÂÄºÔºåÊâßË°åÂõûË∞É
  function onEffect() {
    const newValue = getter();
    cb(newValue, oldValue);
    oldValue = newValue;
  }

  // Ê≥®ÂÜåÂâØ‰ΩúÁî®ÂáΩÊï∞ÔºåÂìçÂ∫îÂºèÊï∞ÊçÆÂèòÂåñÊó∂Ëß¶Âèë
  effect(onEffect);
}

function isRef(r: any) {
  return r && typeof r === 'object' && r.__isRef;
}

function traverse(value: any, seen = new Set()) {
  if (typeof value !== 'object' || value === null || seen.has(value)) return;

  seen.add(value);

  if (isRef(value)) {
    // üëá ÈÄíÂΩíËÆøÈóÆ ref ÁöÑ .value
    traverse(value.value, seen);
  } else {
    for (const key in value) {
      traverse(value[key], seen);
    }
  }

  return value;
}

export { watch };
